@import url(https://use.typekit.net/hji6emp.css);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300&display=swap);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Merriweather", "sans-serif";
  color: #5c5c5c;
  font-size: 14px;
}

p {
  opacity: 0.7;
  line-height: 1.5rem;
  font-size: 1rem;
}

.app-container {
  margin: 0;
  padding-right: 17px;
  padding-left: 17px;
}

.main-content {
  width: 100%;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #9e9e9e;
}

.app__mobileMenuLink {
  font-family: proxima-nova, sans-serif;
  color: #585858;
  border-top: 1px solid #9e9e9e;
  border-bottom: 1px solid #9e9e9e;
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 1rem;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
}

.headerNavReverse {
  display: flex;
  flex-direction: column;
}

.pages__description {
  text-align: center;
  font-size: 14px;
  line-height: 1.9em;
  font-weight: 300;
  overflow-wrap: break-word;
  padding: 17px 0;
}

@media screen and (min-width: 510px) {
  .app__mobileMenuLink {
    display: none;
  }
  .headerNavReverse {
    flex-direction: column-reverse;
  }
}
@media screen and (min-width: 900px) {
  .app-container {
    margin: 80px;
    padding: 100px;
    /* border: 2px solid #9e9e9e; */
    background-color: #fafafa;
  }

  .main-content {
    width: 70%;
    margin: 0 auto;
    margin-top: 25px;
    margin-bottom: 82px;
  }
}
@media screen and (min-width: 500px) {
  .app-container {
    margin: 0;
    padding: 0 17px;
  }
}

.navbar2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  font-weight: 400;
  top: 0;
  visibility: visible;
  overflow: visible;
  transition: all 0.1s ease;
  max-height: 100%;
}

a {
  font-family: proxima-nova, sans-serif;
}

.navbar2__list {
  width: 100%;
  margin: 0.75rem 0;
}

.navbar2__listItem {
  padding: 0.5rem;
}

.navbar2--hide {
  visibility: hidden;
  overflow: hidden;
  max-height: 0;
}

@media screen and (min-width: 510px) {
  .navbar2__list {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.qna__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  margin-bottom: 30px;
}

.title h2 {
  font-weight: bold;
  font-family: Merriweather, sans-serif;
  font-size: 1rem;
  margin-bottom: 0.5rem;
  font-style: italic;
  opacity: 0.7;
}

img {
  width: 100%;
  object-fit: contain;
}

.artcard__wrapper {
  display: flex;
  margin: 17px 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}

.artcard-img {
  width: 100%;
  height: auto;
  object-fit: contain;
}

.caption__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #fff;
  padding: 10%;
}

.caption-title {
  font-weight: 300;
  margin-bottom: 0.3rem;
  font-style: normal;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  font-family: proxima-nova, sans-serif;
}

.caption {
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0em;
  text-transform: none;
  opacity: 0.5;
  margin: 0 auto;
}

