.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header__logo {
  border: transparent;
  width: 70vw;
  object-fit: contain;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.header__title {
  padding: 2rem 0;
  font-family: proxima-nova, sans-serif;
  color: #9e9e9e;
  letter-spacing: 2.5px;
  font-weight: 400;
  line-height: 40px;
}

